<template lang="pug">
    .test__point 
        .test__question-info
            .test__question
            textarea(type='text' v-model='formulaQuestion' rows='3').test__question-content
            vue-mathjax(:formula="formulaQuestion" v-if='texTranslateVisible').formula__question
        ul.test__answers
            li.test__answer-container(v-for="answer,idx in 4" :key="answer.id")
                test-answer-item(
                    :answerChar='answerChars[idx]'
                    :texTranslateVisible='texTranslateVisible'
                )
            .correct
                .buttons
                    button.add__test-item Добавить
                    .latex__on
                        input(type='checkbox' value='latexon' v-model='texTranslateVisible').tex__on
                        span.tex__on-text Включить трансляцию формул
                .correct__answer-content
                    .correct__text Правильный ответ: 
                    select(id='answerChars' name='answerChars' v-model="testAnswer").correct__answer
                        option(value='') Выбрать
                        option(:value='answer' v-for='answer in answerChars') {{answer}}

</template>

<script>
import {VueMathjax} from 'vue-mathjax'
export default {
    components: {
        'vue-mathjax': VueMathjax,
        testAnswerItem: () => import("./new-test-answer-item")
    },
    props: {
        texTranslateVisible: Boolean
    },
    data() {
        return {
            testAnswer: '',
            testInput: '',
            formulaQuestion: '',
            answerChars: ['a', 'b', 'c', 'd']
        }
    },
    mounted() {
        let recaptchaScript = document.createElement('script')
        recaptchaScript.setAttribute('src', 'https://cdnjs.cloudflare.com/ajax/libs/mathjax/2.7.2/MathJax.js?config=TeX-AMS_HTML')
        document.head.appendChild(recaptchaScript)
    },
    methods: {
        checkAnswer() {
            if(this.testInput == this.point.testAnswer) {
                const local = JSON.stringify({
                    index: this.index,
                    answer: this.testInput
                })
                localStorage.setItem(this.index, local) 
            }
        }
    }
}
</script>
